:host .snack-bar.show {
    transform: translateY(0px);
}

:host .snack-bar {
    width: var(--mdx-comp-snackbar-container-sizing-width);
    display: inline-flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: var(--mdx-comp-snackbar-container-sizing-height);
    background-color: var(--mdx-comp-snackbar-container-background-color-default);
    color: var(--mdx-comp-snackbar-container-color-text);
    border-radius: var(--mdx-comp-snackbar-container-border-radius-default);
    font: var(--mdx-comp-snackbar-container-font-default);
    padding: var(--mdx-comp-snackbar-container-padding-vertical) var(--mdx-comp-snackbar-container-padding-horizontal);
    gap: var(--mdx-comp-snackbar-container-gap-horizontal);
    box-shadow: var(--mdx-comp-snackbar-shadow-default);
    transform: translateY(100%);
    transition: transform var(--mdx-sys-motion-time-medium) var(--mdx-sys-motion-easing-ease-in-out);

    @media screen and (max-width: 768px) {
        width: 100%;
    }

    ::slotted([slot='content']) {
        flex-grow: 1;
    }

    &__action-button {
        transition: var(--mdx-comp-snackbar-buttons-button-transition-hover-fade-in-out);
        background-color: var(--mdx-comp-snackbar-buttons-button-background-color-default);
        color: var(--mdx-comp-snackbar-buttons-button-color-default);
        border-style: solid;
        border-color: var(--mdx-comp-snackbar-buttons-button-border-color-default);
        border-width: var(--mdx-comp-snackbar-buttons-button-border-width-default);
        font: var(--mdx-comp-snackbar-buttons-button-font-default);
        border-radius: var(--mdx-comp-snackbar-buttons-button-border-radius-default);
        padding: var(--mdx-comp-snackbar-buttons-button-padding-vertical-default) var(--mdx-comp-snackbar-buttons-button-padding-horizontal-default);
        height: var(--mdx-comp-snackbar-buttons-button-sizing-height);
        width: var(--mdx-comp-snackbar-buttons-button-sizing-width);

        &:hover {
            background-color: var(--mdx-comp-snackbar-buttons-button-background-color-hover);
            color: var(--mdx-comp-snackbar-buttons-button-color-hover);
            border-color: var(--mdx-comp-snackbar-buttons-button-border-color-hover);
        }
    }

    &__close-button {
        transition: var(--mdx-comp-snackbar-buttons-icon-button-transition-hover-fade-in-out);
        border-style: solid;
        border-width: var(--mdx-comp-snackbar-buttons-icon-button-border-width-default);
        border-radius: var(--mdx-comp-snackbar-buttons-icon-button-border-radius-default);
        background-color: var(--mdx-comp-snackbar-buttons-icon-button-background-color-default);
        border-color: var(--mdx-comp-snackbar-buttons-icon-button-border-color-default);
        height: var(--mdx-comp-snackbar-buttons-icon-button-sizing-height);
        width: var(--mdx-comp-snackbar-buttons-icon-button-sizing-width);
        color: var(--mdx-comp-snackbar-buttons-icon-button-icon-color-default);

        &__icon {
            border-width: var(--mdx-comp-snackbar-buttons-icon-button-icon-border-width-default);
            height: var(--mdx-comp-snackbar-buttons-icon-button-icon-sizing-height);
            width: var(--mdx-comp-snackbar-buttons-icon-button-icon-sizing-width);
        }

        &:hover {
            background-color: var(--mdx-comp-snackbar-buttons-icon-button-background-color-hover);
            border-color: var(--mdx-comp-snackbar-buttons-icon-button-border-color-hover);
            color: var(--mdx-comp-snackbar-buttons-icon-button-icon-color-hover);
        }
    }
}