import { Component, Event, EventEmitter, Prop, h } from '@stencil/core';
import { ICON_SIZES } from '../icons/exports';

/**
 * @slot content - For the snack bar conent
 */
@Component({
  tag: 'mdx-snack-bar',
  styleUrl: 'snack-bar.scss',
  shadow: true,
})
export class SnackBar {
  private container?: HTMLDivElement;

  @Event()
  close: EventEmitter<null>;

  @Prop()
  label?: string;

  @Prop()
  closeLabel?: string = 'Close';

  @Event()
  action?: EventEmitter<null>;

  componentDidLoad() {
    setTimeout(() => this.container?.classList.add('show'), 0);
    setTimeout(this.close.emit, 5000);
  }

  render() {
    return (
      <div class="snack-bar" ref={(el) => (this.container = el)}>
        <slot name="content" />
        {this.label && (
          <button class="snack-bar__action-button" name={this.label} onClick={() => this.action?.emit()}>
            {this.label}
          </button>
        )}
        <button class="snack-bar__close-button" aria-label={this.closeLabel} onClick={() => this.close.emit()}>
          <span class="snack-bar__close-button__icon">
            <mdx-icon-x size={ICON_SIZES.XS} />
          </span>
        </button>
      </div>
    );
  }
}
